import React from "react";
import { useHistory } from "react-router-dom";

function Card(props) {
    const history = useHistory();
    function newPage(e) {
        console.log(e.target.name)
        localStorage.setItem("cName", e.target.name);
        history.push("/Company")
    }
    return (
        <div>
            <div className="card" style={{ marginTop: "20px" }}>
                <img className="card-img-top" src={props.img} alt="card-img" />
                <div className="card-body">
                    <h5 className="card-title">{props.name}</h5>
                    <i class="fas fa-rupee-sign">{props.price}</i>
                </div>
                <div className="card-footer">
                    <button className="btn btn-primary btn-lg" name={props.name} onClick={newPage} >Details</button>
                </div>
            </div>
        </div>
    )
}

export default Card;