import React, { useState } from "react";
import { getList } from "../callApi/getList";
import { Link } from "react-router-dom";

function Navbar() {
    const [count, setCount] = useState(0);
    getList(res => {
        setCount(res.length)
    })

    return (
        <div>
            <nav className="navbar ">
                <div className="left" >
                    <Link to="/" className="brand"> <i className="fas fa-wifi" style={{ marginRight: "20px" }}></i></Link>

                    <div> <h1>ISP Hackerearth </h1><small>- Searching ISP made easy!</small></div>

                </div>
                <div className="right" >
                    <h4>Number of ISP:{count}</h4>
                </div>
            </nav>
        </div>
    );
}


export default Navbar;