import React, { useState, useEffect } from "react";
import { getList } from "../callApi/getList";

function Comapny() {
    const [list, setList] = useState([]);
    const [obj, setObj] = useState({});

    useEffect(() => {
        getList((data) => {
            setList([].concat(data));
            var name = localStorage.getItem("cName");
            for (var i = 0; i < list.length; i++) {
                if (list[i].Name === name) {
                    setObj(list[i]);
                    break;
                }
            }
        });

    })
    return (
        <div>

            <iframe width="100%" height="500" src={obj.vid_url} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; " allowfullscreen></iframe>
            <hr />
            <div className="flex">  <h1> Company Name {obj.Name} </h1></div>
            <div className="flex"> </div>
            <br /><br />
            <div className="container">
                <h1> About Company</h1>
                <hr />
                <p>
                    Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
            </p>
            </div>
            <br />

            <div className="container">
                <hr />
                <div className="card-group">
                    <div className="card" >
                        <div className="card-body bg-primary text-white text-center p-3" >
                            <div className="flex">
                                <i class="fas fa-rupee-sign" style={{ fontSize: "50px" }}></i>

                            </div>

                            Only For  {obj.lowest_pice} !
                        </div>
                    </div>
                    <div className="card">
                        <div className="card-body bg-dark text-white text-center p-3">
                            <div className="flex">
                                <i class="far fa-clock" style={{ fontSize: "50px" }}></i>
                            </div>
                            <h4>Speed is {obj.max_speed} MBPS</h4>
                        </div>

                    </div>
                    <div className="card">
                        <div className="card-body bg-warning text-dark text-center p-3">
                            <div className="flex">
                                <i class="fas fa-star" style={{ color: "white" }}></i>
                            </div>
                            <h4>Rating: {obj.rating}/5</h4>
                        </div>
                    </div>
                    <br />
                </div>
            </div>
            <br />
            <br />
            <br />
            <div className="flex">
                <div className="card text-center p-3">
                    <h2> Contact Us!</h2>
                    <hr />
                    <i class="fas fa-phone" style={{ fontSize: "20px" }} > <p>Phone Number {obj.contact_no}</p></i>
                    <hr />
                    <i class="fas fa-envelope-open-text" style={{ fontSize: "20px" }} > <p className="text-muted">Mail Us {obj.email}</p></i>
                    <a href={obj.url} className="btn btn-danger" target="__blank">Visit us</a>
                </div>

            </div>
            <br />
            <br />
            <br />
            <div className="footer">

            </div>
        </div>
    )
}


export default Comapny;